    <yd-cell-group title="Tips">
       
        <yd-cell-item type="label">
            <div slot="left">No Smoking </div>
            <yd-switch slot="right" v-model="nosmokers"></yd-switch>
        </yd-cell-item>

         <yd-cell-item type="label">
            <div slot="left">No Pet </div>
            <yd-switch slot="right" v-model="nopet"></yd-switch>
        </yd-cell-item>

        <yd-cell-item type="label">
            <div slot="left">Subway (Max 10 mins walk) </div>
            <yd-switch slot="right" v-model="subway"></yd-switch>
        </yd-cell-item>

        <yd-cell-item type="label">
            <div slot="left">Shard bathroom? </div>
            <yd-switch slot="right" v-model="livingroom"></yd-switch>
        </yd-cell-item>

    </yd-cell-group>

    <yd-cell-group title="Availability">
       
        <yd-cell-item arrow>
            <span slot="left">Avaliable from: &nbsp;</span>
            <yd-datetime type="date" v-model="startdata" slot="right"></yd-datetime>
        </yd-cell-item>

    </yd-cell-group>

     <yd-cell-group title="Apartment details">
        
        <div class="room_desc_item">
            <span class="item-title">Bedroom : </span>
            <yd-spinner v-model="rooms_num" width="150px" height="40px" min="1" unit="1" max="5" class="item-count"></yd-spinner>
        </div>

        <div class="room_desc_item">
            <span class="item-title">Bathroom : </span>
            <yd-spinner v-model="bath_num" width="150px" height="40px" min="1" unit="1" max="5" class="item-count"></yd-spinner>
        </div>

        
            
    </yd-cell-group>

        <yd-cell-group title="">

            <yd-cell-item>
            <span slot="left"> Size: &nbsp;</span>
            <input slot="right" type="number" placeholder="" v-model="price">
            <span slot="right"> sq.m </span>

            </yd-cell-item>
        <yd-cell-item arrow>
            <span slot="left">District: &nbsp; </span>
                <select slot="right" v-model="district">
                    <option value="xuhui">jingan</option>
                    <option value="jingan">xuhui</option>
                    <option value="pudong">huangpu</option>
                    <option value="huangpu">pudong</option>
                    <option value="pudong">changning</option>
                    <option value="pudong">minhang</option>
                    <option value="pudong">luwan</option>
                    <option value="pudong">hongkou</option>
                    <option value="pudong">baoshan</option>
                    <option value="pudong">zhabei</option>
                    <option value="pudong">songjiang</option>  
                    
                </select>
        </yd-cell-item>
        <yd-cell-item arrow>
            <span slot="left">Subway line: &nbsp; </span>
                <select slot="right" v-model="subwayline">
                    <option value="1">line 1</option>
                    <option value="2">line 2</option>
                    <option value="3">line 3</option>
                    <option value="4">line 4</option>
                    <option value="5">line 5</option>
                    <option value="6">line 6</option>
                    <option value="7">line 7</option>
                    <option value="8">line 8</option>
                    <option value="9">line 9</option>
                    <option value="10">line 10</option>
                    <option value="11">line 11</option>
                    <option value="12">line 12</option> 
                    <option value="13">line 13</option>  
                    
                </select>
        </yd-cell-item>
         <yd-cell-item>
            <span slot="left"> Floor: &nbsp; </span>
            <input slot="right" type="number" placeholder="" v-model="floor" required>
        </yd-cell-item>
       
        <yd-cell-item>
            <span slot="left"> Room N.O: &nbsp; </span>
            <input slot="right" type="number" placeholder="" v-model="room" required>
        </yd-cell-item>
        <yd-cell-item>
            <span slot="left"> Street: &nbsp; </span>
            <input slot="right" type="text" placeholder="" v-model="street" required>
        </yd-cell-item>


    </yd-cell-group>


    <yd-cell-group title="Amenities"> 
                <yd-cell-item arrow @click.native="facilitiesshow = true">
                <span slot="left">Choose what you have</span>
                </yd-cell-item>
    </yd-cell-group>

     <yd-popup v-model="facilitiesshow" position="bottom" height="75%">
    <yd-checklist align="right" v-model="facilities">
        <yd-checklist-item val="Bath">
            <div style="height: 50px;line-height: 50px;">Balcony</div>
        </yd-checklist-item>
        <yd-checklist-item val="Subway">
            <div style="height: 50px;line-height: 50px;">Swimming pool</div>
        </yd-checklist-item>
        <yd-checklist-item val="3">
            <div style="height: 50px;line-height: 50px;">Garden</div>
        </yd-checklist-item>
         <yd-checklist-item val="3">
            <div style="height: 50px;line-height: 50px;">Rooftop</div>
        </yd-checklist-item>
         <yd-checklist-item val="3">
            <div style="height: 50px;line-height: 50px;">Bathtub</div>
        </yd-checklist-item>
         <yd-checklist-item val="3">
            <div style="height: 50px;line-height: 50px;">Gym</div>
        </yd-checklist-item>
         <yd-checklist-item val="3">
            <div style="height: 50px;line-height: 50px;">Oven</div>
        </yd-checklist-item>
        <yd-checklist-item val="3">
            <div style="height: 50px;line-height: 50px;">Dishwasher</div>
        </yd-checklist-item>
        <yd-checklist-item val="3">
            <div style="height: 50px;line-height: 50px;">AC</div>
        </yd-checklist-item>
        <yd-checklist-item val="3">
            <div style="height: 50px;line-height: 50px;">Heating</div>
        </yd-checklist-item>
        <yd-checklist-item val="3">
            <div style="height: 50px;line-height: 50px;">Double pined windows</div>
        </yd-checklist-item>
        <yd-checklist-item val="3">
            <div style="height: 50px;line-height: 50px;">Internet</div>
        </yd-checklist-item>
        <yd-checklist-item val="3">
            <div style="height: 50px;line-height: 50px;">Cable tv</div>
        </yd-checklist-item>
    </yd-checklist>
   
     </yd-popup>


    <yd-cell-group title="Your house pictures">    
            <Vqiniu></Vqiniu>
    </yd-cell-group>

   
    <yd-cell-group title="Term">
        <yd-cell-item>
            <span slot="left"> Price: ¥ &nbsp;</span>
            <input slot="right" type="number" placeholder="" v-model="price">
        </yd-cell-item>
         <yd-cell-item>
            <span slot="left"> Deposit:  &nbsp;</span>
                <select slot="right" v-model="subwayline">
                    <option value="1">1 month rent</option>
                    <option value="2">1.5 month rent</option>        
                </select>        </yd-cell-item>
         
         <yd-cell-item arrow>
            <span slot="left">Lease length: &nbsp; </span>
                <select slot="right" v-model="subwayline">
                    <option value="1">1 year</option>
                    <option value="2">2 year</option>        
                </select>
        </yd-cell-item>

        <yd-cell-item arrow>
            <span slot="left">Payment methods: &nbsp; </span>
                <select slot="right" v-model="subwayline">
                    <option value="1">Per 1 month</option>
                    <option value="2">Per 2 month</option>
                    <option value="2">Per 3 month</option>        
        
                </select>
