    <yd-cell-group title="Describe your awesome room.. ">
        <yd-cell-item>
            <yd-textarea v-model="desc" slot="right" placeholder="Tell me your bank password" maxlength="100"></yd-textarea>
        </yd-cell-item>
    </yd-cell-group>
    

    <yd-button-group>
        <yd-button size='large' type="primary" @click.native="UploadMyApartment">Upload</yd-button>
    </yd-button-group>

        </yd-layout>
  </div>
</template>
<script>
// import District from './address';
import { APIuploadroom } from '../../api/api'
import Vqiniu from '@/components/vqiniu'
import {Radio, RadioGroup,TextArea, CellItem, CellGroup,CitySelect,CheckList,CheckListItem} from 'vue-ydui'
import { mapState ,mapGetters} from 'vuex'
export default {
    components:{
        Radio, 
        RadioGroup,
        CellGroup,
        CellItem,
        Vqiniu,
        CitySelect,
        CheckListItem,
        CheckList,
        TextArea,
        
    },
    data () {
        return {
            shared: true,
            nosmokers: true,
            nopet:true,
            subway:true,
            livingroom:true,

            startdata: '2018-05-11',
            floor:'17',
            room:'1704',
            street:'',
            subwayline:'',
            
            rooms_num:0, // 这里默认是0 注意
            bath_num:0, // 这里默认是0 注意
            district:'',
            files:[],
            price:'',
            deposit:'',

            facilities:[],
            desc:'',
            facilitiesshow:false,

          

        }
    },
    computed: {
        ...mapState([
            'roomimg'
        ])
    },
    methods: {
       UploadMyApartment() {
           APIuploadroom({
            shared: this.shared,
            nosmokers: this.nosmokers,
            nopet:this.nopet,
            subway: this.subway,
            livingroom: this.livingroom,
            startdata: this.startdata,
            floor:this.floor,
            room:this.room,
            street:this.street,
            subwayline:this.subwayline,
            rooms_num:this.rooms_num, // 这里默认是0 注意
            bath_num:this.bath_num, // 这里默认是0 注意
            district:this.district,
            files:this.roomimg,
            price:this.price,
            deposit:this.deposit,
            facilities:this.facilities,
            desc:this.desc,
           }).then( res => {
               console.log(res.data)
           }).catch( err => {
               console.log('出错了！' + err)
           })
       }
    }
  
}
</script>
<style lang="stylus" scoped>
.room_desc_item
    margin 0px auto 10px auto
    .item-title
        vertical-align middle
        width 150px;
        display inline-block
    .item-count
        vertical-align middle

</style>





